export default {
  name: 'UserLogin',
  data() {
    return {
      email: '',
      password: '',
      formData: {
        email: this.email,
        password: this.password,
      },
    }
  },
  methods: {
    async login() {
      console.log(this.formData)
      await this.$store.dispatch('login', this.formData)
      if (this.$store.state.ErrorMsg) {
        this.$router.push('/')
      }
      this.$router.push('/UserHome')
    },
  },
  head() {
    return {
      title: 'User Login Page',
      meta: [
        {
          hid: 'LoginPage',
          name: 'LoginPage',
          content: 'User Can Login Here',
        },
      ],
    }
  },
}
